@import "../../styles/variables";
@import "../../styles/mixins";

//Ici je gère la hateur de la bannière
.home {
	max-height: 120px !important;
	@include responsive(768px) {
		max-height: 223px !important;
	}
}

main {
	margin-bottom: 50px;
}

.container {
	margin: auto;
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 20px;

	@include responsive(768px) {
		max-width: 1240px;
	}

	.card_container {
		width: 100%;
		margin: auto;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		border-radius: 25px;
		gap: 20px;
		@include responsive(840px) {
			background-color: #f6f6f6;
			gap: 60px;
			padding: 60px 0;
		}
	}
}
